.contact-wrapper {
    margin: 7rem 2.5rem 0 2.5rem;
    padding: 0.5rem 0 1rem 0;
    height: 100%;
    background-color: #9d9fa1ef;
    box-sizing: border-box;
    border-radius: 12px;
}

.contact-text, .contact-rights {
    color: steelblue;
    text-align: center;
}

.contact-subtitle{
    font-size: 18px;
}

.contact-icons {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
}

.contact-icons a {
    padding: 7px;
}

.contact-icon-single {
    display: inline-block;
    border-radius: 30px;
    box-shadow: 0px 0px 3px #888;
    padding: 0.5em 0.6em;
    width: 10.4rem !important;
}
